/** @format */

import React from "react";

import ArrowRightAltIcon from "@mui/icons-material/ArrowRightAlt";
import Box from "@mui/material/Box";
import CardContent from "@mui/material/CardContent";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { map } from "lodash";
import { Link } from "react-router-dom";

import pendingApproval from "../../assets/images/signup_successfull.svg";

const SignUpSuccess = (props) => {
  const data = [
    {
      label: "Setup Your Service Plan",
      path: "/dashboard/service-plans",
    },
    {
      label: "Setup Your Appointment",
      path: "/dashboard/appointment",
    },
    {
      label: "Setup Your Questions & Answers Template",
      path: "/dashboard/q-and-a-template",
    },
  ];
  return (
    <>
      <Paper variant="outlined">
        <CardContent>
          <Grid container spacing={1} justifyContent="space-between">
            <Grid item xs={12} md={6} lg={8}>
              <Typography variant="h3" color="secondary" gutterBottom>
                Welcome On InspireMind
              </Typography>
              <Typography
                variant="body1"
                className="mb-3"
                color="text.secondary"
              >
                Your Profile is Under Verification Process, Soon we will be
                update you, meanwhile you can explore rest of the settings that
                will help you to start consultation
              </Typography>

              {map(data, (item, index) => (
                <Stack
                  direction="row"
                  alignItems="center"
                  justifyContent="space-between"
                  spacing={1}
                  mb={1}
                  key={index}
                >
                  <Stack direction="row" alignItems="start" spacing={1}>
                    <ArrowRightAltIcon />
                    <Typography variant="body1">{item.label}</Typography>
                  </Stack>
                  <Link to={item.path}>Setup Now</Link>
                </Stack>
              ))}
            </Grid>
            <Grid item xs={12} md={6} lg={4} xl={3} className="text-center">
              <Box
                component="img"
                src={pendingApproval}
                className="img-fluid mx-auto"
              />
            </Grid>
          </Grid>
        </CardContent>
      </Paper>
    </>
  );
};

export default SignUpSuccess;
